nav {
  & >.offclick {
    background-color: rgba(0,0,0,0.5); }
  & >.container {
    box-shadow: $shadow-2dp; }
  & p {
    font-size: 24px; } }

nav#drawer {
  position: fixed;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3fr;
  z-index: 1;
  box-shadow: 0 -1px -2px 0 rgba(0,0,0,0.5);
  &.hidden {
    display: none; } }

.nav {
  background-color: $secondary;

  &-item {
    display: inline-block;
    list-style: none;
    padding: $space-sm;
    color: $primary;

    &-text {
      color: inherit;
      font-size: 18px;
      text-decoration: none; }

    &--current {
      color: $white;
      background-color: $primary; } } }
